// src/libraries/ToolManager.js

class ToolManager {
    constructor() {
      this.currentTool = "select"; // default tool
    }
  
    setTool(toolName) {
      this.currentTool = toolName;
      if (window.canvasEngine) {
        const engine = window.canvasEngine;
        engine.currentTool = toolName;
    
        // Reset any tool-specific preview data
        engine.isPanning = false;
        engine.isDrawing = false;
        engine.drawStart = null;
    
        //  Clean up rectangle tool residuals
        
        if (engine.previewRect) engine.previewRect = null;
        if (engine.previewInstance) engine.previewInstance = null;
    
        engine.draw(); // Refresh canvas
      }
    
      console.log(` Tool switched to: ${toolName}`);
    }
    
    
  
    getCurrentTool() {
      return this.currentTool;
    }

    
  }
  
  window.ToolManager = new ToolManager(); //  Attach globally
  export default window.ToolManager;
  