// src/panels/PropertyPanel.jsx
import React, { useEffect, useState } from 'react';
import selectionLibrary from '../libraries/SelectionLibrary';
import layerLibrary from '../canvas/LayerLibrary';

const PropertyPanel = () => {
  const [selected, setSelected] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    selectionLibrary.onChange((sel) => {
      setSelected(sel);
      setCurrentIndex(0); // reset to first
    });

    const currentSelection = selectionLibrary.getSelection();
    setSelected(currentSelection);
  }, []);

  if (!selected || selected.length === 0) {
    return <div style={{ color: '#888' }}>No object selected</div>;
  }

  const currentObj = selected[currentIndex];

  const handleChange = (index, property, value) => {
    if (window.canvasEngine) {
      window.canvasEngine.updateSelectedPropertyById(currentObj.id, currentObj.type, property, value);
      const updated = [...selected];
      updated[index] = { ...currentObj, [property]: value };
      setSelected(updated);
    }
  };

  const layerInfo = layerLibrary.getLayerByNumber(
    currentObj.layerNumber || currentObj.layer_number,
    currentObj.datatypeNumber || currentObj.datatype_number || 0
  );

  return (
    <div style={{ padding: '10px', color: '#ddd', fontSize: '14px' }}>
      <h3 style={headerStyle}>Properties ({currentIndex + 1}/{selected.length})</h3>

      <div><b>Object ({currentObj.type})</b></div>

      <div style={row}><strong>Layer:</strong> {layerInfo?.layer_name || 'Unknown'} (L{currentObj.layerNumber}/DT{currentObj.datatypeNumber})</div>

      <div style={row}>
        <label>Layer:</label>
        <select
          value={currentObj.layerId}
          onChange={(e) => handleChange(currentIndex, 'layerId', e.target.value)}
        >
          {Object.values(layerLibrary.getAllLayers()).map((layer) => (
            <option key={layer.id} value={layer.id}>
              {layer.layer_name} (L{layer.layer_number}/DT{layer.datatype_number})
            </option>
          ))}
        </select>
      </div>

      <div style={row}>
        <label>X: </label>
        <input type="number" value={currentObj.x} onChange={(e) => handleChange(currentIndex, 'x', parseFloat(e.target.value))} />
      </div>

      <div style={row}>
        <label>Y: </label>
        <input type="number" value={currentObj.y} onChange={(e) => handleChange(currentIndex, 'y', parseFloat(e.target.value))} />
      </div>

      {currentObj.width !== undefined && (
        <div style={row}>
          <label>Width: </label>
          <input type="number" value={currentObj.width} onChange={(e) => handleChange(currentIndex, 'width', parseFloat(e.target.value))} />
        </div>
      )}

      {currentObj.height !== undefined && (
        <div style={row}>
          <label>Height: </label>
          <input type="number" value={currentObj.height} onChange={(e) => handleChange(currentIndex, 'height', parseFloat(e.target.value))} />
        </div>
      )}

      {currentObj.fontSize !== undefined && (
        <div style={row}>
          <label>Font Size: </label>
          <input type="number" value={currentObj.fontSize} onChange={(e) => handleChange(currentIndex, 'fontSize', parseFloat(e.target.value))} />
        </div>
      )}

      <div style={row}>
        <label>Fill: </label>
        <input type="color" value={currentObj.fill || currentObj.color || '#000000'} onChange={(e) => handleChange(currentIndex, 'fill', e.target.value)} />
      </div>

      <div style={row}>
        <label>Stroke: </label>
        <input type="color" value={currentObj.stroke || '#000000'} onChange={(e) => handleChange(currentIndex, 'stroke', e.target.value)} />
      </div>

      <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '10px' }}>
        <button disabled={currentIndex === 0} onClick={() => setCurrentIndex(currentIndex - 1)}> Prev</button>
        <button disabled={currentIndex === selected.length - 1} onClick={() => setCurrentIndex(currentIndex + 1)}>Next </button>
      </div>
    </div>
  );
};

const headerStyle = {
  fontSize: '16px',
  marginBottom: '10px',
  borderBottom: '1px solid #444',
  paddingBottom: '5px',
};

const row = {
  marginBottom: '8px',
  fontSize: '14px',
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
};

export default PropertyPanel;
