// src/libraries/LabelLibrary.js

import Label from './Label';
const listeners = [];





class LabelLibrary {
  constructor() {
    this.labels = {}; //  New Structure: { cellName: { layerId: [Label, Label, ...] } }

  }

  // Add a new label
  addLabel(cellName, layerId, x, y, text = '', color = '#ffffff') {
    const id = Date.now() + Math.random();
    const label = new Label({ id, text, x, y, layerId, color, type: "label" });
  
    if (!this.labels[cellName]) this.labels[cellName] = {};
    if (!this.labels[cellName][layerId]) this.labels[cellName][layerId] = [];
  
    this.labels[cellName][layerId].push(label);
  
    const cell = window.layoutEngine.getCellByName(cellName);
    if (cell) {
      cell.elements = cell.elements || [];
      cell.elements.push(label);
    }
    return label;
  }
  
  
  // Get all labels (flat array)
  getAllLabels(cellName = null) {
    let all = [];
  
    if (!cellName) return all;
    const cellLabels = this.labels[cellName] || {};
  
    for (const layerId in cellLabels) {
      const layerLabels = cellLabels[layerId];
      for (const label of layerLabels) {
        if (label && typeof label.text === 'string' && label.text.trim() !== '') {
          all.push({
            type: "Label",
            layer_number: parseInt(label.layerId) || 0,
            datatype_number: parseInt(label.datatype_number),
            coordinates: [[label.x, label.y]],
            name: label.text,
            rotation: label.rotation || 0
          });
        }
      }
    }
  
    return all;
  }
  
  
  

  deleteLabel(cellName, layerId, labelId) {
    if (
      !this.labels[cellName] ||
      !this.labels[cellName][layerId]
    ) return;
  
    this.labels[cellName][layerId] = this.labels[cellName][layerId].filter(label => label.id !== labelId);
  }
  

  // Restore full label state from undo/redo
setAllLabels(newLabels) {
  this.labels = {};

  for (let label of newLabels) {
    if (!this.labels[label.layerId]) {
      this.labels[label.layerId] = [];
    }
    this.labels[label.layerId].push(new Label({ ...label }));
  }
}


getLabelById(labelId) {
  for (const layerId in this.labels) {
    const found = this.labels[layerId].find(label => label.id === labelId);
    if (found) return found;
  }
  return null;
}



//  Toggle visibility of a single label
toggleLabelVisibility(labelId) {
  for (const layerId in this.labels) {
    const label = this.labels[layerId].find(l => l.id === labelId);
    if (label) {
      label.visible = !label.visible;
      return;
    }
  }
}


updateLabelText(labelId, newText) {
  for (const layerId in this.labels) {
    const label = this.labels[layerId].find(l => l.id === labelId);
    if (label) {
      label.text = newText;
      return;
    }
  }
}


//  Toggle lock of a single label
toggleLabelLock(labelId) {
  for (const layerId in this.labels) {
    const label = this.labels[layerId].find(l => l.id === labelId);
    if (label) {
      label.locked = !label.locked;
      return;
    }
  }
}

//  Toggle visibility for all labels
toggleAllVisibility(visibleState) {
  for (const layerId in this.labels) {
    this.labels[layerId].forEach(label => label.visible = visibleState);
  }
}

//  Toggle lock for all labels
toggleAllLock(lockState) {
  for (const layerId in this.labels) {
    this.labels[layerId].forEach(label => label.locked = lockState);
  }
}

//  Delete all labels
deleteAllLabels() {
  this.labels = {};
}



updateColorByLayer(layerId, newColor) {
  if (!this.labels[layerId]) return;
  this.labels[layerId].forEach(label => {
    label.color = newColor;
  });
}

drawSingle(ctx, label, offsetX = 0, offsetY = 0, scale = 1) {
  ctx.save();

  //  Assume canvas is already transformed
  ctx.fillStyle = label.color;
  ctx.font = `${label.fontSize}px monospace`;
  ctx.fillText(label.text, label.x, label.y);

  // Plus sign for reference
  const plusSize = 5;
  ctx.strokeStyle = "#00ff00";
  ctx.lineWidth = 1;
  ctx.beginPath();
  ctx.moveTo(label.x - plusSize, label.y);
  ctx.lineTo(label.x + plusSize, label.y);
  ctx.moveTo(label.x, label.y - plusSize);
  ctx.lineTo(label.x, label.y + plusSize);
  ctx.stroke();

  ctx.restore();
}





  // Draw all labels
  drawAllLabels(ctx, offsetX, offsetY, scale, activeLayerId = null, cellName = null, previewLabel=null, extraOffsetX = 0, extraOffsetY = 0) {
    ctx.save();
    ctx.translate(offsetX + extraOffsetX, offsetY+extraOffsetY);
    ctx.scale(scale, scale);
  
    const allLabels = this.getAllLabels(cellName);
  
    for (let label of allLabels) {
      if (label.visible === false) continue; 
      ctx.save(); // Save per label
  
      ctx.fillStyle = label.color;
      ctx.font = `${label.fontSize}px monospace`;
  
      // 1 Draw label text
      ctx.fillText(label.text, label.x, label.y);
  
      // 2  Draw small plus sign (+) after text draw
      ctx.restore(); // reset scaling
  
      ctx.save(); // Save context for plus sign
  
      ctx.setTransform(1, 0, 0, 1, 0, 0); // Reset full transform
      ctx.translate(offsetX + label.x * scale, offsetY + label.y * scale); // manually position
      const plusSize = 5; // 5px size
      ctx.strokeStyle = "#00ff00";
      ctx.lineWidth = 1;
      ctx.beginPath();
      ctx.moveTo(-plusSize, 0);
      ctx.lineTo(plusSize, 0);
      ctx.moveTo(0, -plusSize);
      ctx.lineTo(0, plusSize);
      ctx.stroke();
  
      ctx.restore(); // Restore after plus sign
  
      ctx.save(); // Again save for selection box (important)
  
      // 3  Draw dashed selection box if selected
      ctx.translate(offsetX, offsetY);
      ctx.scale(scale, scale);

      
  
      if (label.selected) {
        ctx.fillStyle = label.color;
        ctx.font = `${label.fontSize}px monospace`;
        const width = ctx.measureText(label.text).width;
        const height = label.fontSize;
        ctx.strokeStyle = "#ffffff";
        ctx.lineWidth = 2 / scale;
        ctx.setLineDash([4, 2]);
        ctx.strokeRect(label.x - 4, label.y - height - 2, width + 8, height + 8);
        ctx.setLineDash([]);
      }
  
      ctx.restore(); // Restore per label
    }
  
    ctx.restore();
  }
  

  // Clear all labels (optional future)
  clearAll() {
    this.labels = {};
  }
}

// Create singleton instance
const labelLibrary = new LabelLibrary();
export default labelLibrary;
