const downloadGds = async () => {
  const createdCells = layoutEngine.getCells();
  const uploadedCells = layoutEngine.getUploadedCells();
  const allCells = [...createdCells, ...uploadedCells];

  if (!allCells.length) {
    alert("  No layout to download");
    return;
  }

  const allCellMap = {};
  allCells.forEach(cell => {
    allCellMap[cell.name] = cell;
  });

  const formattedSet = new Set();
  const formattedCells = [];

  const applyTransform = (x, y, transform) => {
    const angle = (transform.rotation || 0) * Math.PI / 180;
    const cos = Math.cos(angle);
    const sin = Math.sin(angle);
    const newX = transform.x + (x * cos - y * sin);
    const newY = transform.y + (x * sin + y * cos);
    return { x: newX, y: newY };
  };

  const getCellCenter = (cell) => {
    const rects = cell.elements?.filter(e => e.type === "rectangle") || [];
    if (!rects.length) return { x: 0, y: 0 };

    let minX = Infinity, maxX = -Infinity, minY = Infinity, maxY = -Infinity;
    rects.forEach(el => {
      minX = Math.min(minX, el.x);
      maxX = Math.max(maxX, el.x + el.width);
      minY = Math.min(minY, el.y);
      maxY = Math.max(maxY, el.y + el.height);
    });

    return {
      x: (minX + maxX) / 2,
      y: (minY + maxY) / 2
    };
  };

  const formatCellForGDS = (cell, parentTransform = { x: 0, y: 0, rotation: 0 }) => {
    const isUploaded = uploadedCells.some(c => c.name === cell.name);
    const properties = [];

    // Rectangles
    for (const el of cell.elements || []) {
      if (el.type === "rectangle") {
        const coords = [
          [el.x, -el.y],
          [el.x + el.width, -el.y],
          [el.x + el.width, -el.y - el.height],
          [el.x, -el.y - el.height]
        ];
        properties.push({
          type: "Rectangle",
          coordinates: coords,
          layer: parseInt(el.layer),
          datatype: parseInt(el.datatype) || 0
        });
      }
    }

    // Labels
    const labels = labelLibrary.getAllLabels(cell.name) || [];
    for (const label of labels) {
      properties.push({
        type: "Text",
        text: label.text || "",
        coordinates: [[label.x, -label.y]],
        rotation: label.rotation || 0,
        layer: parseInt(label.layer),
        datatype: parseInt(label.datatype) || 0
      });
    }

    // Instances
    for (const inst of cell.instances || []) {
      if (inst.ref) {
        const transformed = applyTransform(inst.x, inst.y, parentTransform);

        let origin = [transformed.x, -transformed.y];

        const childCell = allCellMap[inst.ref];

        // Adjust created only
        const isChildUploaded = uploadedCells.some(c => c.name === inst.ref);
        if (!isChildUploaded && childCell) {
          const center = getCellCenter(childCell);
          origin = [transformed.x - center.x, -(transformed.y - center.y)];
        }

        properties.push({
          type: "Instance",
          text: inst.ref,
          origin: [origin],
          rotation: inst.rotation || 0,
          mirror_x: !!inst.mirrorX
        });

        if (childCell && !formattedSet.has(inst.ref)) {
          const nextTransform = {
            x: transformed.x,
            y: transformed.y,
            rotation: (parentTransform.rotation || 0) + (inst.rotation || 0)
          };
          formattedSet.add(inst.ref);
          formattedCells.push(formatCellForGDS(childCell, nextTransform));
        }
      }
    }

    return {
      cellname: cell.name,
      properties
    };
  };

  Object.values(allCellMap).forEach(cell => {
    if (!formattedSet.has(cell.name)) {
      formattedSet.add(cell.name);
      formattedCells.push(formatCellForGDS(cell));
    }
  });

  const finalJson = {
    data: {
      layout_data: {
        units: {
          user_size: 0.0005,
          db_size: 5e-10
        },
        cells: formattedCells
      }
    }
  };

  try {
    const blob = new Blob([JSON.stringify(finalJson)], {
      type: "application/json"
    });

    const formData = new FormData();
    formData.append("file", blob, "layout.json");

    const res = await fetch("http://localhost:7000/convert-json-to-gds", {
      method: "POST",
      body: formData
    });

    if (!res.ok) throw new Error("Server responded " + res.status);

    const gdsBlob = await res.blob();
    const url = window.URL.createObjectURL(gdsBlob);

    const a = document.createElement("a");
    a.href = url;
    a.download = `${ProjectManager.getDesignName() || "layout"}.gds`;
    a.click();
    window.URL.revokeObjectURL(url);
  } catch (err) {
    console.error("Download failed:", err);
    alert(" Couldnt download GDS  check console / server logs");
  }
};







