import layoutEngine from "./layoutEngine";
import layerLibrary from "../canvas/LayerLibrary";

let instances = {}; // key = layerId, value = array of rectangles

export function hexToRgba(color, alpha = 1) {
  if (!color) return 'rgba(0,0,0,0.4)'; // default fallback color

  if (!color.startsWith("#")) {
    const ctx = document.createElement("canvas").getContext("2d");
    ctx.fillStyle = color;
    color = ctx.fillStyle; // browser converts to hex
  }

  const bigint = parseInt(color.replace("#", ""), 16);
  const r = (bigint >> 16) & 255;
  const g = (bigint >> 8) & 255;
  const b = bigint & 255;
  return `rgba(${r}, ${g}, ${b}, ${alpha})`;
}

function deleteRectangle(cellName, layerId, rectId) {
  if (!instances[cellName] || !instances[cellName][layerId]) return;
  instances[cellName][layerId] = instances[cellName][layerId].filter(rect => rect.id !== rectId);
}


const InstanceLibrary = {
  addRectangle: (cellName, layerId, x, y, width, height, options = {}) => {
    const rect = {
      id: options.id || Date.now(),
      type: "rectangle",
      x,
      y,
      width,
      height,
      selected: false,
      layerId,
      rotation: options.rotation || 0,
      layer: options.layerNumber ?? 1,
      datatype: options.datatypeNumber ?? 0,
      layerNumber: options.layerNumber ?? 1,  //  ADD THIS
  datatypeNumber: options.datatypeNumber ?? 0, 
      ...options
    };
  
    if (!instances[cellName]) instances[cellName] = {};
  if (!instances[cellName][layerId]) instances[cellName][layerId] = [];

  const isDuplicate = instances[cellName][layerId].some(r => r.id === rect.id);
  if (!isDuplicate) {
    instances[cellName][layerId].push(rect);
  }

  
  const cell = layoutEngine.getCellByName(cellName);
  if (cell && !options.skipElement) {
    cell.elements = cell.elements || [];
    cell.elements.push({
      ...rect,
      type: "rectangle"   //  Needed for drawInstanceTree
    });
  }
  
  },


  getAllInstances: () => instances,
  
  updateColorByLayer(layerId, newColor) {
    for (const cellName in instances) {
      if (instances[cellName][layerId]) {
        instances[cellName][layerId].forEach(rect => {
          rect.fill = newColor;
        });
      }
    }
  },  
  
  setAllInstances: (newInstances) => {
    // Clear and replace the entire instance map
    instances = JSON.parse(JSON.stringify(newInstances));
  },
  

  getInstancesByLayer: (layerId) => instances[layerId] || [],

  getInstancesByCellAndLayer: (cellName, layerId) => {
    return instances[cellName]?.[layerId] || [];
  },  

  // drawUploadedRectangle(ctx, rect, scale = 1) {
  //   const layerId = `${rect.layer}:${rect.datatype ?? 0}`;
  //   const layer = layerLibrary.getLayerById(layerId);
  //   const fill = layer?.color || "rgba(0,255,0,0.3)";
  //   const stroke = layer?.color || "green";
  
  //   ctx.beginPath();
  //   ctx.fillStyle = fill;
  //   ctx.strokeStyle = stroke;
  //   ctx.lineWidth = 1 / scale;
  
  //   ctx.rect(rect.x, rect.y, rect.width, rect.height);
  //   ctx.fill();
  //   ctx.stroke();
  // },  

  drawUploadedRectangle(ctx, rect, scale = 1) {
    const x = rect.x1 ?? rect.x;
    const y = rect.y1 ?? rect.y;
    const width = rect.x2 ? rect.x2 - rect.x1 : rect.width;
    const height = rect.y2 ? rect.y2 - rect.y1 : rect.height;
  
    const layerId = rect.layerId || `${rect.layer}:${rect.datatype ?? 0}`;
    const layer = layerLibrary.getLayerById(layerId);
    const fill = rect.fill || layer?.color || "rgba(0,255,0,0.3)";
    const stroke = rect.stroke || layer?.color || "green";
  
    ctx.beginPath();
    ctx.fillStyle = fill;
    ctx.strokeStyle = stroke;
    ctx.lineWidth = 1 / scale;
  
    const cx = x + width / 2;
    const cy = y + height / 2;
  
    ctx.save();
    ctx.translate(cx, cy);
    ctx.rotate((rect.rotation || 0) * Math.PI / 180);
    ctx.translate(-cx, -cy);
  
    ctx.rect(x, y, width, height);
    ctx.fill();
    ctx.stroke();
  
    ctx.restore();
  },
  

  drawSingle(ctx, rect, scale = 1) {
    let x, y, width, height;
  
    if (rect.x1 !== undefined && rect.y1 !== undefined) {
      x = rect.x1;
      y = rect.y1;
      width = rect.x2 - rect.x1;
      height = rect.y2 - rect.y1;
    } else {
      x = rect.x;
      y = rect.y;
      width = rect.width;
      height = rect.height;
    }
  
    //  Fetch color from LayerLibrary if not manually set
    const layerId = `${rect.layer ?? rect.layerId}:${rect.datatype ?? rect.datatypeNumber ?? 0}`;
    const layer = layerLibrary.getLayerById(layerId);
    const fill = rect.fill || layer?.color || "rgba(0,255,0,0.3)";
    const stroke = rect.stroke || layer?.color || "green";
  
    ctx.beginPath();
    ctx.fillStyle = fill;
    ctx.strokeStyle = stroke;
    ctx.lineWidth = 1 / scale;
  
    const cx = x + width / 2;
    const cy = y + height / 2;
  
    ctx.save();
    ctx.translate(cx, cy);
    ctx.rotate((rect.rotation || 0) * Math.PI / 180);
    ctx.translate(-cx, -cy);
  
    ctx.rect(x, y, width, height);
    ctx.fill();
    ctx.stroke();
  
    ctx.restore();
  },   

  drawAll: (ctx, offsetX, offsetY, scale, activeCellName, previewRect) => {
    ctx.save();
    ctx.translate(offsetX, offsetY);
    ctx.scale(scale, scale);
  
    const cellLayers = instances[activeCellName] || {};
    const hasInstances = Object.keys(cellLayers).some(layerId => cellLayers[layerId]?.length > 0);
    
    if (hasInstances) {
      for (const layerId in cellLayers) {
        const items = cellLayers[layerId];
        for (const inst of items) {
          ctx.beginPath();
          ctx.fillStyle = inst.fill;
          ctx.strokeStyle = inst.stroke;
          ctx.lineWidth = 1 / scale;
  
          const cx = inst.x + inst.width / 2;
          const cy = inst.y + inst.height / 2;
  
          ctx.save();
          ctx.translate(cx, cy);
          ctx.rotate((inst.rotation || 0) * Math.PI / 180);
          ctx.translate(-cx, -cy);
  
          ctx.rect(inst.x, inst.y, inst.width, inst.height);
          ctx.fill();
          ctx.stroke();
  
          ctx.restore();
        }
      }
    } else {
      const cell = layoutEngine.getCellByName(activeCellName);
      if (cell && cell.elements?.length) {
        for (const el of cell.elements) {
          if (el.type === "rectangle") {
            let x = el.x1 ?? el.x;
            let y = el.y1 ?? el.y;
            let width = el.x2 ? el.x2 - el.x1 : el.width;
            let height = el.y2 ? el.y2 - el.y1 : el.height;
  
            const layer = layerLibrary.getLayerById(`${el.layer}:${el.datatype ?? 0}`);
            const fill = layer?.color || "rgba(0,255,0,0.3)";
            const stroke = layer?.color || "green";
  
            ctx.beginPath();
            ctx.fillStyle = fill;
            ctx.strokeStyle = stroke;
            ctx.lineWidth = 1 / scale;
  
            const cx = x + width / 2;
            const cy = y + height / 2;
  
            ctx.save();
            ctx.translate(cx, cy);
            ctx.rotate((el.rotation || 0) * Math.PI / 180);
            ctx.translate(-cx, -cy);
  
            ctx.rect(x, y, width, height);
            ctx.fill();
            ctx.stroke();
  
            ctx.restore();
          }
        }
      }
    }
  
    if (previewRect) {
      ctx.beginPath();
      ctx.setLineDash([4, 2]);
      ctx.strokeStyle = "#fff";
      ctx.lineWidth = 1 / scale;
      ctx.rect(previewRect.x, previewRect.y, previewRect.width, previewRect.height);
      ctx.stroke();
      ctx.setLineDash([]);
    }
  
    ctx.restore();
  },  
  

  deleteRectangle,
};

export default InstanceLibrary;
